// ag-grid.scss defines structural styles and should be imported first
@import '~ag-grid-community/src/styles/ag-grid.scss';
@import '~ag-grid-community/src/styles/ag-theme-alpine/sass/ag-theme-alpine-mixin.scss';

// TODO: figure out how to get palette colors in here?
$primary-color: #ffffff;
$row-border: #e6e6e6;
$text-color: #595959;

.ag-theme-custom {
    @include ag-theme-alpine(
        (
            background-color: $primary-color,
            border-color: $primary-color,
            header-background-color: $primary-color,
            odd-row-background-color: $primary-color,
            row-border-color: $row-border,
            data-color: $text-color
        )
    );
}

.ag-header-row {
    .ag-header-cell-label {
        display: flex;
        justify-content: center;
        color: black;
    }
}

.ag-row {
    .ag-cell {
        display: flex;
        justify-content: center;
        align-items: center;
        white-space: normal;
        line-height: 1.25;
        border: 0;
        border-right: 0;
        .ag-react-container {
            width: 100%;
            white-space: normal;
        }
        .ag-cell-wrapper {
            width: 100%;
            justify-content: inherit;
        }
    }
    .ag-cell:first-child {
        justify-content: left;
    }
}

.ag-header-cell-menu-button {
    position: absolute;
    right: 18px; // this is 18px because a header cell has 18px of padding to the right
}
