code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

a {
    color: inherit;
    text-decoration: none;
}

a.text-link {
    color: #0000ee;
}

h1,
h2 {
    margin-bottom: 0;
}

.row {
    display: flex;
    width: 100%;
}

[hidden] {
    display: none !important;
}
